<style lang=less>
    @import '../../../assets/index.less';
    @import './baseInfo.less';
</style>
<template>
    <div class="base-info">
        <!-- 基本信息 -->
        <p class="title">
            <span>
            </span>
            {{$t('center.index[2]')}}
        </p>
        <el-form class="base-info-form" :model="baseInfo">
          <!-- 账号 -->
            <el-form-item :label="$t('center.baseInfo[0]')" class="base-info-form-item">
                <el-input disabled v-model="baseInfo.account">
                </el-input>
            </el-form-item>
            <!-- UID -->
            <el-form-item label="UID" class="base-info-form-item">
                <el-input disabled v-model="baseInfo.UID">
                </el-input>
            </el-form-item>
            <!-- 登录密码 -->
            <el-form-item :label="$t('center.baseInfo[1]')" class="base-info-form-item">
                <el-input disabled v-model="baseInfo.loginPwd">
                </el-input>
                <span class="btn" @click="resetPwd">
                    {{$t('center.baseInfo[2]')}}
                </span>
            </el-form-item>
            <!-- 资金密码 -->
            <el-form-item :label="$t('center.baseInfo[3]')" class="base-info-form-item">
                <el-input disabled v-model="baseInfo.moneyPwd">
                </el-input>
                <span class="btn" @click="resetMoneyPwd">
                    {{ baseInfo.hasSetMoneyPwd ? $t('center.baseInfo[2]') :$t('center.baseInfo[4]') }}
                </span>
            </el-form-item>
        </el-form>
        <!-- 账户安全 -->
        <p class="title">
            <span>
            </span>
            {{$t('center.baseInfo[5]')}}
        </p>
        <el-form class="base-info-form" :model="safety">
            <!-- 安全等级 -->
            <!-- <el-form-item :label="$t('center.baseInfo[6]')" class="base-info-form-item">
                <el-input disabled v-model="safety.level">
                </el-input>
                <span class="btn">
                    {{$t('center.baseInfo[7]')}}
                </span>
                <i class="tips">
                    （{{$t('center.baseInfo[8]')}}）
                </i>
            </el-form-item> -->
            <!-- 邮箱 -->
            <el-form-item :label="$t('center.baseInfo[9]')" class="base-info-form-item">
                <el-input disabled v-model="safety.email">
                </el-input>
                <span v-if="safety.email == $t('center.baseInfo[10]')" class="btn" @click="bindEmail">
                    {{$t('center.baseInfo[11]')}}
                </span>
                <span v-else class="btn">
                  {{$t('center.baseInfo[12]')}}
                </span>
                <i class="tips">
                    （{{$t('center.baseInfo[13]')}}）
                </i>
            </el-form-item>
            <!-- 手机号 -->
            <el-form-item :label="$t('center.baseInfo[14]')" class="base-info-form-item">
                <el-input disabled v-model="safety.tel">
                </el-input>
                <span v-if="safety.tel == $t('center.baseInfo[15]')" class="btn" @click="bindTel">
                  {{$t('center.baseInfo[16]')}}
                </span>
                <span v-else class="btn">
                  {{$t('center.baseInfo[17]')}}
                </span>
                <i class="tips">
                  （{{$t('center.baseInfo[18]')}}）
                </i>
            </el-form-item>
            <!-- 身份验证 -->
            <el-form-item :label="$t('center.baseInfo[19]')" class="base-info-form-item">
                <el-input disabled v-model="safety.identityVerify">
                </el-input>
                <span v-if="safety.ableGoIdentityVerify" class="btn" @click="goIdentityVerify">
                    {{ safety.identityVerifyText }}
                </span>
                <span v-else class="btn">
                    {{ safety.identityVerifyText }}
                </span>
                <i class="tips">
                  （{{$t('center.baseInfo[20]')}}）
                </i>
            </el-form-item>
            <!-- 谷歌验证 -->
            <el-form-item :label="$t('center.baseInfo[21]')" class="base-info-form-item">
                <el-input disabled v-model="safety.gooleVerify">
                </el-input>
                <span class="btn" v-if="!safety.isGooleVerify" @click="bindGoole">{{$t('center.baseInfo[22]')}}</span>
                <!-- v-if="safety.isGooleVerify && safety.isGooleOpen" -->
                <span v-if="safety.isGooleVerify && safety.isGooleOpen" class="btn"  @click="closeGoole">
                  {{$t('center.baseInfo[23]')}}
                </span>
                <!-- v-if="safety.isGooleVerify && !safety.isGooleOpen" -->
                <span v-if="safety.isGooleVerify && !safety.isGooleOpen" class="btn"  @click="openGoole">{{$t('center.baseInfo[24]')}}</span>
                <i class="tips">
                  （{{$t('center.baseInfo[25]')}}）
                </i>
            </el-form-item>
        </el-form>
        <!-- 登录历史列表 -->
        <p class="title">
            <span>
            </span>
            {{$t('center.baseInfo[26]')}}
        </p>
        <el-table :data="loginHistoryList" style="padding-bottom: 15px;">
            <el-table-column prop="time" :label="$t('center.baseInfo[27]')" align="left">
            </el-table-column>
            <el-table-column prop="ip" :label="$t('center.baseInfo[28]')" align="left">
            </el-table-column>
            <el-table-column prop="AgentInfo" :label="$t('center.baseInfo[29]')" align="left">
            </el-table-column>
        </el-table>
        <!-- 谷歌验证开启关闭弹窗 -->
        <el-dialog :title="dialog.title" :visible.sync="dialogVisible" :close-on-click-modal="false" width="420px">
            <el-form class="base-info-dialog">
                <el-form-item :label="$t('center.baseInfo[30]')">
                    <el-input v-model="dialog.gooleCode" :placeholder="$t('center.baseInfo[31]')">
                    </el-input>
                </el-form-item>
                <el-form-item v-if="isShowTelCode" class="tel-code" :label="telCodeText">
                    <el-input v-model="dialog.telCode" :placeholder="$t('center.baseInfo[32]')+telCodeText">
                    </el-input>
                    <span v-loading="dialog.getCodeLoading" element-loading-spinner="el-icon-loading" class="get-code" @click="getCodeRequest">
                        {{ dialog.telCodeText }}
                    </span>
                </el-form-item>
                <div class="btn-wrap">
                    <el-button class="btn" type="primary" @click="verify">
                      {{$t('center.baseInfo[33]')}}
                    </el-button>
                    <el-button class="btn" type="primary" plain @click="dialogVisible=false">
                      {{$t('center.baseInfo[34]')}}
                    </el-button>
                </div>
            </el-form>
        </el-dialog>
    </div>
</template>
<script>
import {userInfo,sendMsg,sendEmailMsg} from '@/api/public.js'
import {loginHistory,setGoogleState} from "@/api/user.js"
export default {
  data () {
    return {
      dialogVisible:false,
      isTelLogin: this.$public.checkMobile(sessionStorage.getItem('mobile')),
      dialogTitle: this.$t('center.baseInfo[35]'),
      isShowTelCode: false,
      telCodeText: this.$t('center.baseInfo[36]'),
      baseInfo:{
        account:'',//账号
        UID:'',//UID
        loginPwd:'',//登录密码
        moneyPwd:'******',//资金密码
        hasSetMoneyPwd:false
      },
      safety:{
        level:'',//安全等级
        email:'',//邮箱
        tel:'',//手机号
        identityVerify:'',//身份验证
        identityVerifyText: this.$t('center.baseInfo[37]'), //身份验证状态按钮提示语
        ableGoIdentityVerify:true, //去验证按钮能否使用
        status:'', //初级身份验证的状态
        heigherStatus:'', //高级身份验证的状态
        gooleVerify:'',//谷歌验证
        isGooleVerify:true,
        isGooleOpen:true
      },
      dialog:{
        title: this.$t('center.baseInfo[38]'),
        gooleCode:'',
        telCode:'',
        telCodeText: this.$t('center.baseInfo[39]'),//验证码处显示的内容
        countDownNumber:60,//倒计时秒数
        countDownTimer:null,//定时器
        ableGetCode:true,//验证码按钮能否点击
        getCodeLoading:false//验证码按钮加载状态
      },
      loginHistoryList:[],
      sessionData:{},
    }
  },
  created () {
    this.baseInfo.account = this.$public.phoneEncryption(sessionStorage.getItem('mobile')) //账号
    this.getUserInfo()
    this.sessionData = JSON.parse(sessionStorage.getItem('userInfo'))
    loginHistory().then(res => {
      if(res.data.status == 200){
        this.loginHistoryList = res.data.data.login_log.slice(0,3)
      } else{
        this.$public.msg(res.data.msg,'error',this)
      }
    })
    this.telCodeText = this.isTelLogin ? this.$t('center.baseInfo[40]') : this.$t('center.baseInfo[41]')
  },
  watch: {
  },
  methods: {
    bindTel(){
      this.$router.push({
        path: "/center/baseInfo/bindTel",
      })
    },
    bindEmail(){
      this.$router.push({
        path: "/center/baseInfo/bindEmail",
      })
    },
    resetPwd(){
      this.$router.push({
        path: "/center/baseInfo/resetPwd",
      })
    },
    resetMoneyPwd(){
      this.$router.push({
        path: "/center/baseInfo/maneyPwd",
      })
    },
    bindGoole(){
      this.$router.push({
        path: "/center/baseInfo/gooleVerify",
      })
    },
    goIdentityVerify(){
      // this.heigherStatus
      if(this.status !=3){
        this.$router.push({
          path: "/center/identity/lowerIdentity",
        })
      } else if(this.status == 3){
        this.$router.push({
          path: "/center/identity/heigherIdentity",
        })
      }
      
    },
    closeGoole(){
      this.isShowTelCode = true
      this.dialog.title = this.$t('center.baseInfo[42]')
      this.dialogVisible = true
    },
    openGoole(){
      this.dialog.title = this.$t('center.baseInfo[43]')
      this.dialogVisible = true
    },
    closeDialog(){
      this.dialog.gooleCode = ''
      this.dialog.telCode = ''
      this.dialogVisible=false
    },
    verify(){
      if(!this.$public.code(this.dialog.gooleCode)){
        this.$public.msg(this.$t('center.baseInfo[44]'),'warning',this)
        return
      }

      // if(this.isShowTelCode && !this.$public.code(this.dialog.telCode)){
      //   this.$public.msg('手机验证码填写有误！','warning',this)
      //   return
      // }

      let _data = {
        dyGoodleCommand: this.dialog.gooleCode,  //谷歌验证码
        mobile: sessionStorage.getItem('mobile'),//手机号或邮箱
        act: this.isShowTelCode ? 0 : 1,         //0 关 1开
      }
      if(this.isShowTelCode){
        _data.code = this.dialog.telCode
      }
      setGoogleState(_data).then((res)=>{ //更改谷歌验证状态
        if(res.data.status == 200){
          this.getUserInfo()//设置成功后 sessionStorage随之更新
          this.$public.msg(this.$t('center.baseInfo[45]'),'success',this)
          this.dialogVisible=false
        } else{
          this.$public.msg(res.data.msg,'error',this)
        }
      },
      ()=>{
        this.$public.msg(this.$t('center.baseInfo[46]'),'error',this)
      })
    },
    getUserInfo(){
      userInfo().then(res => {
        if(res.data.status == 200){
          sessionStorage.setItem('userInfo', JSON.stringify(res.data.data))
          this.baseInfo.UID = res.data.data.uid //UID
          this.baseInfo.loginPwd = res.data.data.opwd //登录密码
          this.baseInfo.hasSetMoneyPwd = res.data.data.tpwd //是否已设置过资金密码
          // this.baseInfo.moneyPwd = res.data.data.???? //资金密码

          if(res.data.data.user_level == 1){ //安全等级
            this.safety.level = this.$t('center.baseInfo[47]')
          } else if(res.data.data.user_level == 2){
            this.safety.level = this.$t('center.baseInfo[48]')
          } else if(res.data.data.user_level == 3){
            this.safety.level = this.$t('center.baseInfo[49]')
          }
          this.safety.email = res.data.data.email ? this.$t('center.baseInfo[50]') : this.$t('center.baseInfo[51]') //邮箱
          this.safety.tel = res.data.data.mobile ? this.$t('center.baseInfo[50]') : this.$t('center.baseInfo[51]') //手机号
          let status = this.status = res.data.data.status
          let heigherStatus = this.heigherStatus = res.data.data.auth_status
          // let status = this.status = 3
          // let heigherStatus = this.heigherStatus = 1
          if(status == 1){ //初级身份验证
            this.safety.identityVerify = this.$t('center.baseInfo[52]')
            this.safety.identityVerifyText = this.$t('center.baseInfo[53]')
            this.safety.ableGoIdentityVerify = true
          } else if(status == 2){
            this.safety.identityVerify = this.$t('center.baseInfo[54]')
            this.safety.identityVerifyText = this.$t('center.baseInfo[54]')
            this.safety.ableGoIdentityVerify = false
          } else if(status == 3){
            this.safety.identityVerify = this.$t('center.baseInfo[55]')
            this.safety.identityVerifyText = this.$t('center.baseInfo[56]')
            this.safety.ableGoIdentityVerify = true
          } else if(status == 4){
            this.safety.identityVerify = this.$t('center.baseInfo[57]')
            this.safety.identityVerifyText = this.$t('center.baseInfo[58]')
            this.safety.ableGoIdentityVerify = true
          }

          if(heigherStatus == 1 && status == 3){ //高级身份验证
            this.safety.identityVerify = this.$t('center.baseInfo[59]')
            this.safety.identityVerifyText = this.$t('center.baseInfo[60]')
            this.safety.ableGoIdentityVerify = true
          } else if(heigherStatus == 2 && status == 3){
            this.safety.identityVerify = this.$t('center.baseInfo[61]')
            this.safety.identityVerifyText = this.$t('center.baseInfo[62]')
            this.safety.ableGoIdentityVerify = false
          } else if(heigherStatus == 3 && status == 3){
            this.safety.identityVerify = this.$t('center.baseInfo[63]')
            this.safety.identityVerifyText = this.$t('center.baseInfo[63]')//没有写错！
            this.safety.ableGoIdentityVerify = false
          } else if(heigherStatus == 4 && status == 3){
            this.safety.identityVerify = this.$t('center.baseInfo[64]')
            this.safety.identityVerifyText = this.$t('center.baseInfo[65]')
            this.safety.ableGoIdentityVerify = true
          }
          this.safety.isGooleVerify = res.data.data.command != 0  //谷歌验证
          this.safety.gooleVerify = res.data.data.command != 0 ? this.$t('center.baseInfo[50]') : this.$t('center.baseInfo[51]')
          this.safety.isGooleOpen = res.data.data.is_start_google != 0
        } else{
          this.$public.msg(res.data.msg,'error',this)
        }
        
      });
    },
    countDown(){//验证码倒计时
      let countDownCopy = this.dialog.countDownNumber
      this.dialog.countDownTimer = setInterval(()=>{
        if(this.dialog.countDownTimer && countDownCopy<=0){
          clearInterval(this.dialog.countDownTimer)
          this.dialog.telCodeText = this.$t('center.baseInfo[66]')//重新发送
          this.dialog.ableGetCode = true
          return
        }
        this.dialog.telCodeText = countDownCopy
        countDownCopy--; 
      },1000)
    },
    getCodeRequest(){//发送获取验证码请求接口
      if(!this.dialog.ableGetCode){return}
      this.dialog.ableGetCode = false
      this.dialog.getCodeLoading = true
      if(this.isTelLogin){//手机登录下发送短信验证码
        let _data = {
          mobile: sessionStorage.getItem('mobile'),
          mcode: this.sessionData.mcode,
          type: 7
        }
        sendMsg(_data).then((res)=>{
          this.dialog.getCodeLoading = false
          if(res.data.status == 200){
            this.$public.msg(this.$t('center.baseInfo[67]'),'success',this)
            this.countDown()
          } else{
            this.$public.msg(res.data.msg,'error',this)
            this.dialog.ableGetCode = true
          }
        },
        ()=>{
          this.$public.msg(this.$t('center.baseInfo[68]'),'error',this)
          this.dialog.ableGetCode = true
          this.dialog.getCodeLoading = false
        })
      } else{//发送邮箱验证码
        let _data = {
          email: sessionStorage.getItem('mobile')
        }
        sendEmailMsg(_data).then((res)=>{
          this.dialog.getCodeLoading = false
          if(res.data.status == 200){
            this.$public.msg(this.$t('center.baseInfo[67]'),'success',this)
            this.countDown()
          } else{
            this.$public.msg(res.data.msg,'error',this)
            this.dialog.ableGetCode = true
          }
        },
        ()=>{
          this.$public.msg(this.$t('center.baseInfo[68]'),'error',this)
          this.dialog.ableGetCode = true
          this.dialog.getCodeLoading = false
        })
      }
      
    }
  }
}
</script>



